.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar-brand-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 0;
  padding-bottom: 0;

  .navbar-icon {
    width: 2em;
    height: 2em;
    margin: auto;
  }

  .navbar-title {
    margin-left: 1em;

    .row:first-child {
      font-weight: bold;
    }

    .row:nth-child(2) {
      color: gray;
      font-size: calc(1.5em / 2);
    }
  }
}

.card-img {
  width: 100%;
}

.card-img-overlay-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  font-weight: bold;

  .row:first-child {
  }

  .row:nth-child(2) {
  }
}

.text-black-outline {
  -webkit-text-stroke: 1px black;
}

.header-home {
  background-size: cover;
  background-position: initial !important;
}

.col-button {
  width: 100%;
  background-color: transparent;
  outline: none;
}

.col-button-img {
  height: 5em;
  background-size: cover;
  /* background-position: center; */
}
